import VueRouter from "vue-router";

const routes = [
  {
    path: "/screen",
    name: "screen",
    component: () => import("@/view/ScreenIndex2.vue"),
    meta: {
      keepAlive: true,
    },
  },
  {
    path: "/",
    name: "login",
    // component: () => import("../components/login/myLogin.vue"),
    component: () => import("@/view/ScreenIndex2.vue"),
    meta: {
      keepAlive: false,
    },
  },
  {
    path: "/index",
    name: "index",
    component: () => import("../components/myIndex.vue"),
    meta: {
      keepAlive: false,
    },
    children: [
      {
        path: "/deviceLog",
        name: "deviceLog",
        meta: {
          title: "设备日志",
        },
        component: () => import("../components/main/deviceLog.vue"),
      },
      {
        path: "/switchesInf",
        name: "switchesInf",
        meta: {
          title: "交换机信息",
        },
        component: () =>
          import("../components/main/shebeixinxi/switchesInf.vue"),
      },
      {
        path: "/hostInf",
        name: "hostInf",
        meta: {
          title: "交换机信息",
        },
        component: () => import("../components/main/shebeixinxi/hostInf.vue"),
      },
      {
        path: "/switchesData",
        name: "switchesData",
        meta: {
          title: "数据信息",
        },
        component: () =>
          import("../components/main/shebeixinxi/switchesData.vue"),
      },
      {
        path: "/flowtableInf",
        name: "flowtableInf",
        meta: {
          title: "流表信息",
        },
        component: () => import("../components/main/liubiao/flowtableInf.vue"),
      },
      {
        path: "/flowtableIssued",
        name: "flowtableIssued",
        meta: {
          title: "下发流表",
        },
        component: () =>
          import("../components/main/liubiao/flowtableIssued.vue"),
      },
      {
        path: "/rightsManagement",
        name: "rightsManagement",
        meta: {
          title: "权限管理",
        },
        component: () => import("../components/main/rightsManagement.vue"),
      },
    ],
  },
];
const router = new VueRouter({
  mode: "history",
  routes,
});
export default router;
